<template>
  <div class="left-sort">
       <div class="left-sort-bg">
           <a class="left-sort-item"  :class="currentIndex === index ? 'em' : ''" v-for="(item, index) in leftsortdata" :key="index"  @click="sortBarClick(item, index)" >
               <span class="em-text"> {{ item.title }}</span>
           </a>
       </div>
  </div>
</template>

<script>

export default {
  name: "LeftSort",
  props:{
    leftsortdata:{
        type: Array,
        default() {
            return [];
        }
    }
  },
  data(){
      return{
          currentIndex:0
      }
  },
  methods:{
      sortBarClick(item, index){
        //拿到当前索引index值和需要请求item.maitKey值
        const obj = {
            maitKey: item.maitKey,
            index
        };
        this.currentIndex = index;
        this.$emit("sortBarItemClick", obj);
      }
  }

};
</script>
<style lang="scss" scoped>
.left-sort{
    position: absolute;
    overflow: auto;
    width:70px;
    top:0;
    bottom: 0;
    left: 0;
    overflow-scrolling: touch;
    -webkit-overflow-scrolling: touch;
    .left-sort-bg{
        background-color: #fff;
        .left-sort-item{
        position: relative;
        margin-bottom: 1px;
        display: block;
        height:60px;
        background-color: #F7F7F7;
        box-sizing: border-box;
        line-height:60px;
        color: #333;
        font-size:14px;
        text-align: center;
        }
        .em{
            background-color: #fff;
            span{ color: #2ba4fa;}  
         }
        .em:before {
            content: '';
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            width: 3px;
            background-color: #2ba4fa;
        }
        .em-text{
            position: absolute;
            padding: 10px 12px;
            top: 50%;
            left: 50%;
            width:60px;
            line-height: 1.2;
            transform: translate(-50%, -50%);
        }
    }

}
</style>