import {request} from "./request";

export function getLeftSort() {
  return request({
    url: "/category"
  })
}

export function getLeftSortpage(maitKey) {
    return request({
      url: "/subcategory",
      params: {
        maitKey
      }
    })
}



export function getCategoryDetail(miniWallkey, type) {
    return request({
      url: "/subcategory/detail",
      params: {
        miniWallkey,
        type
      }
    })
}
  