<template>
  <div class="my-sort">

    <!--头部-->
    <head-menu class="sort-header">
      <i class="iconfont icon-back" slot="left" @click="backsortPage"></i>
      <search-box slot="center"  placeholder="开学季有礼，好货5折起" findtxt @click.native="goToSearch"/>
    </head-menu>
     <div class="sort-pre">
          <!--左侧导航-->
          <left-sort :leftsortdata="sortList" @sortBarItemClick="sortBarItemClick"></left-sort>
          <!--右侧内容-->
          <div class="right-sort">
                  <!--第一屏数据-->
                <sort-con :category-list="sortLeftToRight"></sort-con>
                  <!--导航数据-->
                <tab-menu :tabmenulist="tabsortTitles" @tabClick="tabsortClick" ref="tabSort" ></tab-menu>
                 <!--商品数据-->
                <goods-list :goods="sortgoryDetailList" class="sortgoods"></goods-list>
          </div>

     </div>
  </div>
</template>

<script>
//内部引用
import LeftSort from "views/sort/modular/LeftSort";
import SortCon from "views/sort/modular/SortCon";

//外部组件
import HeadMenu from "components/common/headmenu/HeadMenu";
import SearchBox from "components/content/search/SearchBox";
import TabMenu from "components/common/tabmenu/TabMenu";
import GoodsList from "components/content/goods/GoodsList";
import { getLeftSort, getLeftSortpage, getCategoryDetail } from "@/network/sort";
export default {
  name: "MySort",
  components:{
    HeadMenu,
    SearchBox,
    LeftSort,
    SortCon,
    TabMenu,
    GoodsList
  },
  data(){
     return{
       tabsortTitles: ["流行", "新款", "精选"],
        // 分类侧边栏数据
        sortList: [],
        //右边数据
        sortLeftToRight:[],
        sortgoryDetailList: [],
        currentIndex: 0
     }
  },
  created() {
    this.getLeftSortlist();
  },
   beforeCreate() {
    // 进入详情页隐藏tabBar
    this.$store.commit("setTabBarShow", false);
  },
   // 路由离开的时候恢复tabBar
  beforeRouteLeave(to, from, next) {
    this.$store.commit("setTabBarShow", true);
    next();
  },
  methods:{
    //数据请求操作-----------------------------------------------------------------------------------------------------------------------------

      getLeftSortlist(){
           getLeftSort().then(res => {
            //第一步先拿到左侧数据并赋值
            this.sortList = res.data.category.list;
            this.$nextTick(() => {  //this.$nextTick用于延迟执行
              //第二步，异步赋值完后，获取左侧maitKey值数据，在给getLeftSortpage端口请求数据
              this.getLeftSortpage(this.sortList[0].maitKey);
              //第一步（二屏数据）异步赋值完后，获取左侧miniWallkey值数据，在给getCategoryDetail端口请求数据
              this.getCategoryDetail(this.sortList[0].miniWallkey, "pop");
            });
          });
      },
     //第三步拿到maitKey值后请求数据并合并一起，然后给右侧内容赋值
     getLeftSortpage(key) {
        getLeftSortpage(key).then(res => {
          this.sortLeftToRight = [...res.data.list];
        });
     },
     //点击左侧导航按钮
     sortBarItemClick({maitKey, index}){
          // 防止反复请求
          if (this.currentIndex === index) return;
              this.currentIndex = index;
          // 每次切换分类初始化tabControl的下标
              this.$refs.tabSort.curIndex = 0;
          // 请求对应的推荐列表    
          this.getLeftSortpage(maitKey)

          this.getCategoryDetail(this.sortList[this.currentIndex].miniWallkey, "pop");

      },
      //第二步（二屏数据）拿到miniWallkey和pop值后请求数据并合并一起，然后给右侧内容赋值
      getCategoryDetail(key, type) {
          getCategoryDetail(key, type).then(res => {
            //console.log(res);
            this.sortgoryDetailList = [...res];
          });
      },

      tabsortClick(index){
            const typeList = ["pop", "new", "sell"];
            // 切换类型数据
            this.getCategoryDetail(this.sortList[this.currentIndex].miniWallkey, typeList[index]);
      },


    //结束数据请求操作-----------------------------------------------------------------------------------------------------------------------------
    //跳转搜索页
    goToSearch(){
       this.$router.push('/search');
    },
    //返回上一页
     backsortPage(){
        this.$router.push('/');
     },
    

  },

  

};
</script>
<style lang="scss" scoped>
.my-sort{
      width: 100%;
      height:100vh;
    .sort-header{
        background: linear-gradient(90deg,#27a6fa,#8e71f5);
        background: -webkit-linear-gradient(left,#27a6fa,#8e71f5);
        color: #fff;
    }
    .sort-pre{
      position: relative;
      box-sizing: border-box;
      width: 100%;
      height: 100%;
      margin: 0;
      padding: 0;
      .right-sort{
        position: absolute;
        overflow: auto;
        left: 70px;
        right: 0;
        top:0;
        bottom: 0;
        padding: 8px 10px 0;
        overflow-scrolling: touch;
        -webkit-overflow-scrolling: touch;
        }
    }
    .sortgoods{
      ::v-deep .goods-list-img {
          height: auto;
        }
    }

}

</style>